import React, { memo, useState } from 'react';
import { View, StyleSheet, ActivityIndicator, Platform } from 'react-native';
import { Image as ExpoImage, ImageContentFit } from 'expo-image';
import { DESIGN_TOKENS } from '@/constants/designSystem';

interface OptimizedImageProps {
  source: { uri: string } | number;
  contentFit?: ImageContentFit;
  aspectRatio?: number;
  width?: number | string;
  height?: number | string;
  borderRadius?: number;
  placeholder?: string; // blurhash
  priority?: 'low' | 'normal' | 'high';
  loading?: 'lazy' | 'eager';
  alt?: string;
  onLoad?: () => void;
  onError?: () => void;
  style?: any;
}

/**
 * –û–ø—Ç–∏–º–∏–∑–∏—Ä–æ–≤–∞–Ω–Ω—ã–π –∫–æ–º–ø–æ–Ω–µ–Ω—Ç –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è
 * 
 * –û—Å–æ–±–µ–Ω–Ω–æ—Å—Ç–∏:
 * - –ê–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–∞—è –æ–ø—Ç–∏–º–∏–∑–∞—Ü–∏—è –¥–ª—è WebP/AVIF
 * - Lazy loading –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é
 * - Placeholder —Å blurhash
 * - –ò–Ω–¥–∏–∫–∞—Ç–æ—Ä –∑–∞–≥—Ä—É–∑–∫–∏
 * - –û–±—Ä–∞–±–æ—Ç–∫–∞ –æ—à–∏–±–æ–∫
 * - Responsive sizing
 * 
 * @example
 * <OptimizedImage
 *   source={{ uri: imageUrl }}
 *   contentFit="cover"
 *   aspectRatio={16/9}
 *   placeholder={blurhash}
 *   loading="lazy"
 * />
 */
function OptimizedImage({
  source,
  contentFit = 'cover',
  aspectRatio,
  width = '100%',
  height,
  borderRadius = 0,
  placeholder,
  priority = 'normal',
  loading = 'lazy',
  alt,
  onLoad,
  onError,
  style,
}: OptimizedImageProps) {
  const [isLoading, setIsLoading] = useState(true);
  const [hasError, setHasError] = useState(false);

  const handleLoad = () => {
    setIsLoading(false);
    onLoad?.();
  };

  const handleError = () => {
    setIsLoading(false);
    setHasError(true);
    onError?.();
  };

  // –û–ø—Ä–µ–¥–µ–ª—è–µ–º –ø—Ä–∏–æ—Ä–∏—Ç–µ—Ç –∑–∞–≥—Ä—É–∑–∫–∏
  const fetchPriority = priority === 'high' ? 'high' : priority === 'low' ? 'low' : 'auto';

  return (
    <View
      style={[
        styles.container,
        {
          width,
          height: height || (aspectRatio ? undefined : '100%'),
          aspectRatio,
          borderRadius,
        },
        style,
      ]}
    >
      <ExpoImage
        source={source}
        contentFit={contentFit}
        placeholder={placeholder}
        transition={200}
        onLoad={handleLoad}
        onError={handleError}
        style={[
          styles.image,
          {
            borderRadius,
          },
        ]}
        // Web-specific optimizations
        {...(Platform.OS === 'web' && {
          // @ts-ignore - web-specific props
          loading,
          fetchpriority: fetchPriority,
          alt: alt || '',
          decoding: 'async',
        })}
        // –ö—ç—à–∏—Ä–æ–≤–∞–Ω–∏–µ
        cachePolicy="memory-disk"
        // –ü—Ä–∏–æ—Ä–∏—Ç–µ—Ç
        priority={priority}
      />

      {/* –ò–Ω–¥–∏–∫–∞—Ç–æ—Ä –∑–∞–≥—Ä—É–∑–∫–∏ */}
      {isLoading && !hasError && (
        <View style={styles.loadingContainer} testID="optimized-image-loading">
          <ActivityIndicator
            size="small"
            color={DESIGN_TOKENS.colors.primary}
          />
        </View>
      )}

      {/* –ó–∞–≥–ª—É—à–∫–∞ –ø—Ä–∏ –æ—à–∏–±–∫–µ */}
      {hasError && (
        <View style={[styles.errorContainer, { borderRadius }]} testID="optimized-image-error">
          <View style={styles.errorIcon}>
            <View style={styles.errorIconInner} />
          </View>
        </View>
      )}
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    position: 'relative',
    overflow: 'hidden',
    backgroundColor: DESIGN_TOKENS.colors.backgroundSecondary,
  },
  image: {
    width: '100%',
    height: '100%',
  },
  loadingContainer: {
    ...StyleSheet.absoluteFillObject,
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: DESIGN_TOKENS.colors.backgroundSecondary,
  },
  errorContainer: {
    ...StyleSheet.absoluteFillObject,
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: DESIGN_TOKENS.colors.mutedBackground,
  },
  errorIcon: {
    width: 48,
    height: 48,
    borderRadius: 24,
    backgroundColor: DESIGN_TOKENS.colors.disabled,
    justifyContent: 'center',
    alignItems: 'center',
  },
  errorIconInner: {
    width: 24,
    height: 2,
    backgroundColor: DESIGN_TOKENS.colors.disabledText,
    transform: [{ rotate: '45deg' }],
  },
});

export default memo(OptimizedImage);

/**
 * –£—Ç–∏–ª–∏—Ç–∞ –¥–ª—è –≥–µ–Ω–µ—Ä–∞—Ü–∏–∏ srcset –¥–ª—è responsive images
 * @param baseUrl - –ë–∞–∑–æ–≤—ã–π URL –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è
 * @param sizes - –ú–∞—Å—Å–∏–≤ —Ä–∞–∑–º–µ—Ä–æ–≤ [320, 640, 1024, 1920]
 * @returns srcset —Å—Ç—Ä–æ–∫–∞
 */
export function generateSrcSet(baseUrl: string, sizes: number[] = [320, 640, 1024, 1920]): string {
  return sizes
    .map(size => {
      // –ü—Ä–µ–¥–ø–æ–ª–∞–≥–∞–µ–º, —á—Ç–æ –Ω–∞ —Å–µ—Ä–≤–µ—Ä–µ –µ—Å—Ç—å endpoint –¥–ª—è —Ä–µ—Å–∞–π–∑–∞
      const resizedUrl = `${baseUrl}?w=${size}&q=80&fm=webp`;
      return `${resizedUrl} ${size}w`;
    })
    .join(', ');
}

/**
 * –£—Ç–∏–ª–∏—Ç–∞ –¥–ª—è –≥–µ–Ω–µ—Ä–∞—Ü–∏–∏ sizes attribute
 * @param breakpoints - –û–±—ä–µ–∫—Ç —Å breakpoints –∏ —Ä–∞–∑–º–µ—Ä–∞–º–∏
 * @returns sizes —Å—Ç—Ä–æ–∫–∞
 */
export function generateSizes(breakpoints: Record<string, string> = {}): string {
  const defaultSizes = {
    mobile: '100vw',
    tablet: '50vw',
    desktop: '33vw',
  };

  const sizes = { ...defaultSizes, ...breakpoints };

  return [
    `(max-width: ${DESIGN_TOKENS.breakpoints.mobile}px) ${sizes.mobile}`,
    `(max-width: ${DESIGN_TOKENS.breakpoints.tablet}px) ${sizes.tablet}`,
    sizes.desktop,
  ].join(', ');
}
